<template>
    <div class="container">
        <div v-for="news in news" :key="news.id">
            <router-link :to="{path:'/news/'+news.id, query:{newsIdArr}}">
                <div class="box">
                    <img :src="news.imgUrl">
                    <div class="info">
                        <p class="title">{{news.title}}</p>
                        <p class="news-info">
                            <span>作者：{{news.author}}</span>
                            <span>分类：{{news.category}}</span>
                            <i class="iconfont icon-pinglun"></i>
                            <span class="comments">评论 {{news.comments.length}}</span>
                        </p>
                    </div>
                </div>
            </router-link>
        </div>
        <div class="search-tips">
            <h2>>>内容为空>></h2>
        </div>
    </div>
</template>

<script>


export default {
    data(){
        return {
            news: [],
            matchText: '',
            newsIdArr: [],
            empty: false,
        }
    },
    mounted(){
        this.filterNews();
        this.showEmpty();
    },
    beforeRouteUpdate(to, from, next) {
        next();   
        this.filterNews();
        this.showEmpty();
    },
    methods: {
        filterNews(){
            this.news = this.$store.state.news;
            this.matchText = this.$route.query;
            // 分类检索
            if(this.matchText.category){
                this.news = this.news.filter((news) => {
                    return news.category == this.matchText.category ;
                })
            }
            // 文本检索
            else{
                this.news = this.news.filter((news) => {
                    return news.title.match(this.matchText.searchText)   
                })
            }
            // 路由改变newsIdArr依旧存在，所以要进行初始化
            this.newsIdArr = [];
            this.news.forEach(news => {
                this.newsIdArr.push(parseInt(news.id));
            });
        },
        // 是否显示提示"内容为空"
        showEmpty(){
            let emptyEl = document.querySelector('.search-tips');
            let ishave = emptyEl.classList.contains('empty');

            if(!this.news.length && !ishave)
            emptyEl.classList.add('empty');
            if(this.news.length && ishave)
            emptyEl.classList.remove('empty');
        }
    },
    components: {
        
    }
}
</script>

<style scoped>
    p{
        margin: 0;
        padding: 0;
    }
    .container{
        margin: 0 auto;
        width: 90vw;
        min-height: 400px;
    }
    .box{
        height:200px;
        width: 800px;
        background-color: rgba(0, 0, 0, .02);
        border: 1px dotted rgba(0, 0, 0, .1);
        border-radius: 10px;
        margin-bottom: 25px;
    }
    img{
        float: left;
        height: 160px;
        padding: 20px 0 20px 10px;
    }
    .info{
        margin: 0;
        padding: 20px 0 0 20px;
        float:left;
    }
    .title{
        width: 350px;
        height: 80px;
        line-height: 35px;
        font-size: 20px;
        color: #666;
    }
    .news-info{
        height: 60px;
        line-height: 60px;
        font-size: 12px;
        color: #999;
    }
    i{
        margin-right: 3px;
        font-size: 12px;
    }
    .news-info span{
        margin-right: 12px;
    }
    .search-tips{
        display: none;
    }
    .search-tips h2{
        text-align: center;
        color: #666;
        padding-top: 50px;
    }
    .search-tips.empty{
        display: block;
    }
</style>


