<template>
    <div>
        <canvas id="myCanvas"></canvas>
    </div>
</template>

<script>
export default {
    data(){
        return {
            ballArr: []
        }
    },
    mounted(){
        let canvas = document.getElementById('myCanvas');
        this.canvasInit(canvas);
        this.canvasAnimate();
    },
    methods: {
        // 初始化画布大小，实例化小球数组
        canvasInit(canvas){
            canvas.width = document.documentElement.clientWidth;
            canvas.height = 500;

            for(let i=0; i<10; i++){
                let radius = Math.random() * 10 + 5;
                let x = Math.random() * canvas.width;
                let y = Math.random() * 500;
                let dx = Math.random()-0.5;
                let dy = Math.random()-0.5;
                let color = '#' + Math.random().toString(16).slice(2, 8);
                this.ballArr.push(new this.Ball(x, y, dx, dy, radius, color));
            }
        },

        // 小球构造器
        Ball(x, y, dx, dy, radius, color, canvas, ctx){
            this.x = x;
            this.y = y;
            this.dx = dx;
            this.dy = dy;
            this.radius = radius;
            this.color = color;

            this.draw = function(canvas, ctx){
                // x方向运动，
                if(this.x + this.radius > canvas.width || this.x < this.radius){
                    this.dx = -this.dx;
                }
                this.x += this.dx;
                // y方向运动
                if(this.y + this.radius > 500 || this.y < this.radius){
                    this.dy = -this.dy;
                }
                this.y += this.dy;

                ctx.beginPath();
                ctx.arc(this.x, this.y, this.radius, 0, 2*Math.PI);
                ctx.fillStyle = this.color;
                ctx.fill();
                ctx.closePath();
            }
        },
        canvasAnimate(){
            window.requestAnimationFrame(this.canvasAnimate);
            let canvas = document.getElementById('myCanvas');
            let ctx = canvas.getContext('2d');
            ctx.clearRect(0, 0, canvas.width, canvas.height); 
            // 原理？？？？
            this.ballArr.forEach(el => {
                el.draw(canvas, ctx);
            })
             
        }
    }
}
</script>


