<template>
  <div id="app">
    <newsHeader/>
    <router-view></router-view>
    <footInfo/>
  </div>
</template>

<script>

import newsHeader from "./components/newsHeader"
import store from '@/vuex/store'
import footInfo from './components/footInfo'

export default {
  name: 'App',
  components: {
      newsHeader,
      footInfo
  },
  mounted(){
    window.addEventListener('beforeunload', this.saveNews)
  },

  methods: {
    saveNews(){
      sessionStorage.setItem('state', JSON.stringify(this.$store.state))
    }
  },
  store
}
</script>

<style>
  body{
    margin: 0;
    padding: 0;
  }
  html{
    overflow-y: scroll;
  }
  /* .router-view{
    display: block;
    width: 1080px;
    height: 100%;
  } */
</style>



