<template>
    <div class="top">
        <nav class="nav">
            <h1>Storm新闻</h1>
            <div class="left-nav">
                    <router-link to="/">首页</router-link>
                    <div v-for="nav in navs" :key="nav.key">
                        <router-link :to="{path:'/search/'+nav.key, query:nav}">{{nav.category}}</router-link>
                    </div>
                    <router-link to="/">更多</router-link>
                <div class="about-me">
                    <div class="slide-up" @click="toUser">
                        <div class="i-box">
                            <i class="fas fa-user"></i>
                            <p>个人中心</p>
                        </div>
                    </div>
                    <div class="slide-up">
                        <div class="i-box">
                            <i class="fas fa-star"></i>
                            <p>我的关注</p>
                        </div>
                    </div>
                    <div class="slide-up">
                        <div class="i-box">
                            <i class="fas fa-envelope"></i>
                            <p>信息</p>
                        </div>
                    </div>
                </div>
            </div>
        </nav>
        <div class="search">
            <input class="search-input" type="text" v-model="searchText" placeholder="搜索">
            <router-link :to="{path:'/search/'+searchText, query:{searchText}}">
                <button class="search-btn"><i class="fa fa-search"></i></button>
            </router-link>
        </div>
    </div>
</template>

<script>

import store from '@/vuex/store'

export default {
    name: 'newsHeader',
    data(){
        return{
            news: [],
            searchText: '',
            navs: [
                {key: 1, category:'游戏'},
                {key: 2, category:'工业'},
                {key: 3, category:'商业'},
                {key: 4, category:'科技'},
            ]  
        }
    },
    // mounted(){
    //     this.nav.forEach((nav) => {
    //         nav.uri = encodeURI(nav.category)
    //         console.log(nav.uri)
    //     })
    // },
    // methods: {
    //     jump(){
    //         console.log(1)
    //         let str = 'hello'
    //         console.log(str)
    //         this.$router.push( {name:'searchPage', query:{searchText:str}} )
            
    //     }
    // }
    methods: {
        toUser(){
            if(!this.$store.state.user.status)
            this.$router.push('/signin');
        }
    },
    store   
}
</script>

<style>
    ul, li, p,h1{
        margin: 0;
        padding: 0;
    }
    .left-nav a{
        text-decoration: none;
        color: #fff;
        margin-right: 20px;
        padding: 10px;
        font-size: 14px;
        float: left;
        transition: 0.3s;
    }
    .top{
        width: 100vw;
    }
    .nav{
        display: grid;
        align-items: center;
        grid-template-columns: 1fr 1fr;
        margin: 0 auto;
        background-color: #203d80;
        height:80px;
    }
    .nav h1{
        font-size: 28px;
        padding-left: 120px;
        color: #fff;
        font-weight: bold;
        letter-spacing: 2px;
    }
    .left-nav{
        display: flex;
    }
    .left-nav a:hover{
        background-color: #fff;
        color: #000;
        border-radius: 5px;
    }
    /*  */
    .about-me{
        display: flex;
        margin-left: 20px;
    }
    .slide-up{
        height: 30px;
        width: 55px;
        overflow: hidden;
        margin-right: 10px;
        cursor: pointer;
    }
    .slide-up i{
        color: #fff;
        font-size: 16px;
        height: 30px;
        line-height: 30px;
        text-align: center;
        width: 55px;
    }
    .slide-up p{
        height: 30px;
        line-height: 30px;
        width: 55px;
        background-color: #fff;
        font-size: 12px;
        text-align: center;
    }
    .i-box{
        transition: 0.3s ease-in-out;
    }
    .i-box:hover{
        transform: translateY(-30px)
    }
    /*  */
    .search{
        margin: 20px 120px 20px 0;
        height: 50px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
    .search-input{
        height: 23px;
        width: 200px;
        outline: none;
        padding: 2px 0 2px 7px;
        border: 1px solid #ccc;
    }
    .search-btn{
        height: 29px;
        width: 50px;
        border: 0;
        background-color: crimson;
        color: #fff;
        font-size: 12px;
        outline: none;
    }
    

    @media (max-width:1300px){
        .nav, .search{
            width: 1300px;
        }
    }
</style>


