<template>
    <div class="container">
        <myCanvas id="canvas"></myCanvas>
        <div class="input-wrapper">
            <div class="anno">
                <h3>
                    <i class="fa fa-user"></i>
                    {{tips}}，请登录
                </h3>
                <form data-rule='maxlength:12|minlength:6|pattern:"^[a-zA-Z0-9]"'>
                    <input id="name" type="text" placeholder="请输入账号" onfocus="placeholder=''" onblur="placeholder='请输入账号'" v-model="nameValue">
                    <p class="warning hide"><i class="fa fa-times"></i>账号格式错误！</p>
                    <input id="password" type="text" placeholder="请输入密码" onfocus="placeholder=''" onblur="placeholder='请输入密码'" v-model="psdValue">
                    <p class="warning hide"><i class="fa fa-times"></i>密码格式错误！</p>
                    <div class="checkbox-inner">
                        <input type="checkbox" class="checkb">
                        <span class="checkbox_text">记住密码！</span>
                    </div>
                    <button id="anno_btn">登录</button>
                </form>
            </div>
        </div>
    </div>
</template>

<script>

import myCanvas from '@/components/myCanvas'

export default {
    data(){
        return {
            tips: '',
            nameValue: '',
            psdValue: '',
            rule: {},
            checkName: true,
            checkPsd: true,
        }
    },
    mounted(){
        this.sayHello();
        this.parserule();
        
        // 验证账号输入
        let nameInputEl = document.getElementById('name');
        nameInputEl.addEventListener('blur', () => {
            this.checkName = this.showWarning(nameInputEl, this.nameValue);
        })
        // 验证密码输入
        let psdInputEl = document.getElementById('password');
        psdInputEl.addEventListener('blur', () => {
            this.checkPsd = this.showWarning(psdInputEl, this.psdValue);
            }
        )
    },
    methods: {
        // 根据时间显示问好
        sayHello(){
            let date = new Date();
            let hour = date.getHours();
            if(hour < 5)
            this.tips = "早点休息";
            if(hour >= 5 && hour < 8)
            this.tips = "早上好";
            if(hour >= 8 && hour < 11)
            this.tips = "上午好";
            if(hour >= 11 && hour < 13)
            this.tips = "中午好";
            if(hour >= 13 && hour < 18)
            this.tips = "下午好";
            if(hour >= 18)
            this.tips = "晚上好";
        },

        // 解析规则
        parserule(){
            let ruleStr = document.querySelector('form').getAttribute('data-rule');
            let ruleArr = ruleStr.split('|');
            ruleArr.forEach(el => {
                let ruleItem = el.split(':');
                this.rule[ruleItem[0]] = JSON.parse(ruleItem[1]);
            })
        },
        // 验证器
        validate(){
            // 验证最长长度
            function validate_maxlength(value, rule){
                return value.length <= rule.maxlength;
            }
            // 验证最短长度
            function validate_minlength(value, rule){
                return value.length >= rule.minlength;
            }
            // 验证正则
            function validate_pattern(value, rule){
                let reg = new RegExp(rule.pattern)
                return reg.test(value);
            }

            return function(value, rule){
                // 验证所有模式
                for(let key in rule){
                    var resultTemp = eval('validate_' + key)(value, rule);
                    if(!resultTemp)
                    return false;
                }
                return true;
            }          
        },
        // 根据验证结果是否显示警告

        showWarning(el, value){
            let result = this.validate()(value, this.rule);
            let ishave = el.nextElementSibling.classList.contains('hide');

            if(result && !ishave)
            el.nextElementSibling.classList.add('hide');
            else if(!result && ishave)
            el.nextElementSibling.classList.remove('hide');
        },

        
    },
    components: {
        myCanvas
    }
}
</script>

<style scoped>
    #canvas{
        position: absolute;
        z-index: -1;
    }
    .input-wrapper{
        display: flex;
        justify-content: center;
    }
    .anno{
        height: 450px;
        width: 350px;
        background-color: #fefefe;
        border: 1px dotted rgba(0, 0, 0, 0.5);
        border-radius: 10px;

    }
    h3{
        margin-top: 40px;
        padding-left: 20px;
        letter-spacing: 1px;
        color: #444;
    }
    i{
        padding-right: 10px;
    }
    form{
        padding: 30px 0 0 40px;
        align-items: center;
    }
    #name, #password{
        display: block;
        height: 30px;
        width: 250px;
        padding: 2px 8px;
        border: 1.1px solid #999;
        outline: none;
        border: 0;
        border-bottom: 1px solid #adadad;
        background-color: inherit;
        font-size: 12px;
    }
    #name{
        margin-top: 10px;
    }
    .warning.hide{
        visibility: hidden;
    }
    p{
        height: 35px;
        line-height: 30px;
        color: #ff3b30;
        font-size: 12px;
        letter-spacing: 0.5px;
        padding-left: 8px;
        margin-bottom: 10px;
    }
    .checkb, .checkbox_text{
        font-size: 12px;
        color: #888;
        letter-spacing: .5px;
    }
    .checkbox_text{
        display: block;
        margin-left: 2px;
    }
    .checkbox-inner{
        display: flex;
    }
    #anno_btn{
        margin-top: 10px;
        border: 0;
        background: linear-gradient(120deg, #203d80, #8e44ad);
        border-radius: 4px;
        height: 40px;
        width: 268.2px;
        color: #fff;
        cursor: pointer;
    }
    
</style>


