<template>
    <div>
        <div class="container">
            <div class="single-page">
                <div class="title">{{news.title}}</div>
                <img :src="news.imgUrl">
                <p class="content">{{news.content}}</p>
                <div class="info">
                    <span>作者：{{news.author}}</span>
                    <span>分类：{{news.category}}</span>
                    <i class="iconfont icon-pinglun"></i>
                    <span class="comment">评论：{{commentsCount}}</span>
                </div>
            </div>
            <div class="prev-next">
                <router-link :to="{path:'/news/'+prev.id, query:{newsIdArr}}" v-if="prev">上一条：{{prev.title}}</router-link>
                <router-link :to="{path:'/news/'+next.id, query:{newsIdArr}}" v-if="next">下一条：{{next.title}}</router-link>
            </div>
            <div class="comments">
                <div class="add-comments">
                    <p class="comments-add">添加评论</p>
                    <textarea class="comments-textArea" v-model="value" placeholder="留下您的足迹吧..." onblur="placeholder='留下您的足迹吧...'" onfocus="placeholder=''"></textarea>
                    <button class="comments-btn" @click="addComments">添加</button>
                </div>
                <p class="comments-all">全部评论</p>
                <div class="comments-box" v-if="commentsCount">
                    <div v-for="comment in comments" :key="comment.text" class="comments-item">
                        <p class="comments-text">{{comment.text}}</p>
                        <p class="comments-date">{{comment.date}}</p>
                    </div>
                </div>
                <div class="add-remind" v-if="!commentsCount">
                    <p>还没有评论，赶快抢个板凳吧>></p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import store from '@/vuex/store'
import { mapState } from 'vuex'


export default {
    name: 'singlePage',
    data(){
        return {
            news: '',
            id: '',
            comments: '',
            value: '',
            newsIdArr: [],
            next: 0,
            prev: 0,
            commentsCount: 0
        }
    },
    // mounted(){
    //     console.log(1)
    //     event.$on('deliver-news', (data) => {
    //         this.news = data;
    //         console.log(this.news)
    //     })
    // },
    beforeRouteUpdate (to, from, next) {
        next();
        this.loadContent();
    },
    mounted(){
        // 获取数据
        this.newsIdArr= this.$route.query.newsIdArr;  
        this.newsArr = this.$store.state.news;
        this.loadContent();
    },
    methods: {
        addComments(){
            if(this.value){
                let date = new Date();
                let dateStr = date.toLocaleDateString();
                this.$store.commit('setComments', {id:this.id, newComments:{text:this.value, date:dateStr}})
                this.value = '';
                this.commentsCount++;
            }
        },
        loadContent(){
            this.id = this.$route.params.id;
            this.news = this.newsArr[this.id]; 
            this.comments = this.news.comments;
            this.commentsCount = this.comments.length;
            // 获取上一条下一条新闻
            // 分类和搜索的下一条
            if(this.newsIdArr){
                for(let i=0; i<this.newsIdArr.length; i++){
                    if(this.newsIdArr[i] == this.id){
                        let prevId = this.newsIdArr[i-1];
                        this.prev = i-1>=0? this.newsArr[prevId]: null;
                        let nextId = this.newsIdArr[i+1];
                        this.next = i+1<this.newsIdArr.length? this.newsArr[nextId]: null;
                        break;
                    }
                }
            }
            // 展示全部时的下一条新闻
            else{
                let prevId = parseInt(this.id)-1;
                this.prev = prevId>=0? this.newsArr[prevId]: null;
                // this.id是一个字符串
                let nextId = parseInt(this.id)+1;
                this.next = nextId<this.newsArr.length? this.newsArr[nextId]: null;
            }
        }
    },
    components:{
        
    },
    store
}
</script>

<style scoped>
    p{
        margin: 0;
        padding: 0;
    }
    .container{
        width: 90vw;
        margin: 0 auto;
        border: 1px dotted rgba(0, 0, 0, .1);
        background-color: rgba(0, 0, 0, .02);
        border-radius: 10px;
    }
    .single-page{
        margin: 0;
    }
    .title{
        height: 80px;
        line-height: 80px;
        font-size: 21px;
        color: #444;
        padding-left: 20px;
        background-color: rgba(0, 0, 0, .02)
    }
    img{
        margin: 20px 0 0 20px;
    }
    .content{
        margin: 35px 0 0 20px;
        text-indent: 2em;
        font-size: 17px;
        line-height: 30px;
        width: 700px;
        color: #333;
    }
    .info{
        margin: 40px 0 20px 20px;
        font-size: 13px;
        color: #666;
    }
    .info span{
        height: 20px;
        line-height: 20px;
        margin-right: 20px;
    }
    .info .comment{
        margin-left: 10px;
    }
    .info i{
        font-size: 12px;
    }
    .add-comments{
        height: 200px;
    }


    .prev-next{
        margin-top: 40px;
        padding-left: 10px;
    }
    .prev-next a{
        display: block;
        height: 40px;
        line-height: 40px;
        font-size: 14px;
        color: #666;
        text-decoration: none;
        cursor: pointer;
        letter-spacing: 1px;
        width: 500px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }


    .comments{
        margin-top: 60px;
    }
    .comments-add{
        background-color: #eee;
        height: 50px;
        line-height: 50px;
        background-color: #379be9;
        padding-left: 10px;
        font-size: 16px;
        width: 150px;
        color: #fff;
        letter-spacing: 1px;
    }
    ::-webkit-input-placeholder{
        color: #999;
        font-size: 13px;
        letter-spacing: 1px;
    }
    .comments-textArea{
        display: block;
        padding: 5px;
        height: 80px;
        width: 500px;
        margin-top: 20px;
        float: left;
        resize: none;
        outline-style: none;
        border: 1px solid #379be9;
    }
    .comments-textArea:focus{
        border: 1px solid #379be9;
        background-color: rgba(0, 0, 0, .01)
    }
    .comments-btn{
        margin-top: 20px;
        height: 92px;
        width: 92px;
        float: left;
        border: none;
        color: #fff;
        font-size: 16px;
        background-color: #379be9;
        cursor: pointer;
    }
    .comments-box{
        display: block;
        min-height: 100px;
        margin-bottom: 100px;
    }
    .comments-item{
        margin: 20px 0;
    }
    .comments-box .comments-date{
        text-align: right;
        padding-right: 40px;
        height: 20px;
        letter-spacing: 1px;
        line-height: 20px;
        font-size: 12px;
        color: #666;
        border-bottom: 1px dotted #ccc;
    }
    .comments-box .comments-text{
        padding: 10px 20px;
        text-indent: 2em;
        line-height: 30px; 
        min-height: 60px;
        width: 600px;
        color: #666;

    }
    .comments-all{
        margin-top: 40px;
        width: 640px;
        letter-spacing: 1px;
        height: 50px;
        line-height: 50px;
        padding-left: 10px;
        font-size: 18px;
        color: #000;
        background-color: #ccc;
    }
    .add-remind{
        height: 150px;
        width: 640px;
        text-align: center;
    }
    .add-remind p{
        padding-top: 60px;
        color: #666;
        letter-spacing: 1px;
        font-size: 14px;
    }
</style>


