import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)


const state = sessionStorage.getItem('state')? JSON.parse(sessionStorage.getItem('state')): 
{
    news: '', 
    user: {status: false}
}

const mutations = {
    getNews(state, news){
        state.news = news
    },
    setComments(state, commentsObj){
        let id = commentsObj.id;
        let newComments = commentsObj.newComments;
        state.news[id].comments.push(newComments);
    },
}

export default new Vuex.Store({
    state,
    mutations
})


