<template>
    <div class="show">
        <div class="container">
            <h2 class="newst">最新资讯</h2>
            <div class="middle">
                <div class="news-box">
                    <div v-for="news in news" :key="news.id">
                        <router-link :to="{path:'/news/'+news.id}">
                            <div class="news-wrapper">
                                <img :src="1" :data-src="news.imgUrl">
                                    <div class="info">
                                        <p class=title>{{news.title}}</p>
                                        <p class="news-info">
                                            <span>作者：{{news.author}}</span>
                                            <span>分类：{{news.category}}</span>
                                            <i class="iconfont icon-pinglun"></i>
                                            <span class=comments>评论 {{news.comments.length}}</span>
                                        </p>
                                    </div>
                                </div>
