            </div>
            <div class="page-line">
                <div class="next-page">
                    <i class="fa fa-angle-double-left"></i>
                </div>
                <div class="prev-page">
                    <i class="fas fa-angle-double-right"></i>
                </div>
            </div>
        </div>
    </div>
</template>

