                author: "Saber",
                category: "科技",
                imgUrl: "https://img3.jiemian.com/101/original/20200304/158332133642955800_a480x270.jpg",
                content: "",
                comments: []
            },{
                id: 9,
                title: "信用卡发卡量和交易量呈断崖式下滑，疫情之下银行要如何破局？",
                author: "Rider",
                category: "商业",
                imgUrl: "https://img2.jiemian.com/101/original/20200305/158339556273289600_a480x270.jpg",
                content: "",
                comments: []
            },{
                id: 10,
                title: "中国最大核电上市公司董事长辞职",
                author: "Assassin",
                category: "工业",
                imgUrl: "https://img3.jiemian.com/101/original/20200303/15832271445298400_a480x270.jpg",
                content: "",
                comments: []
            },{
                id: 11,
                title: "三一重工近一年股价上涨超140%，市值一度突破1600亿",
                author: "Assassin",
                category: "工业",
                imgUrl: "https://img1.jiemian.com/101/original/20200303/158322437272688400_a480x270.png",
                content: "",
                comments: []
            }   
                ],
            searchText: '',
        }
    },
    created(){
        this.$store.commit('getNews', this.news);
        this.pageIdFilter();
    },
    mounted(){
        this.loadImg();
        window.addEventListener('scroll', this.lazyLoad(this.loadImg, 500));
    },
    methods: {
        // 节流
        lazyLoad(fn, delay){
            let timer;
            return function(){
                if(timer) return;
                timer = setTimeout(() => {
                    fn();
                    timer = null;
                }, delay)
            }
        },
        // 修改src属性加载图片 
        loadImg(){
            let newsElArr = document.querySelectorAll('.news-wrapper');
            Array.from(newsElArr).forEach(el => {
                let top = el.getBoundingClientRect().top;
                if(top <= window.innerHeight){
                    el.firstElementChild.setAttribute('src', el.firstElementChild.getAttribute('data-src'));
                };
            })
        },
        // 每页9条新闻
        pageIdFilter(){
            let pageID = this.route? this.route.query.pageID: 1;
            this.news = this.news.slice((pageID-1)*9, pageID*9);
        },
        toUser(){
            if(!this.$store.state.user.status)
            this.$router.push('/signin');
        }

    },
    components:{
        
    },
    store
}
</script>

<style scoped>
    *{
        margin: 0;
        padding: 0;
    }
    .show{
        overflow: hidden;
    }
    .header{
        height: 100px;
    }
    .newst{
        height: 80px;
        width: 180px;
        font-size: 24px;
        padding-left: 15px;
        letter-spacing: 1px;
        line-height: 80px;
        color: #666;
        background-color: rgba(0, 0, 0, .1)
    }
    .container{
        margin: 0 auto;
        width: 1200px;
        display: block;
    }
    .middle{
        margin-top: 20px;
        overflow: hidden;
    }
    .news-wrapper{
        margin: 0 30px 30px 0;
        float: left;
        height: 48vh;
        width: 30%;
        border-radius: 10px;
        background-color: rgba(0, 0, 0, .01);
        border: 1px dotted rgba(0, 0, 0, .1);
    } 
    .news-wrapper img{
        padding: 10px;
        width: 90%;
    }
    .title{
        width: 90%;
        padding: 20px 10px;
        font-size: 17px;
        color: #444;
        height: 60px;
        line-height: 30px; 
    }
    .title:hover{
        color: #2e6e9e;
    }
    .news-info{
        height: 20px;
        line-height: 20px;
        padding: 0 10px;
        font-size: 12px;
        color: #999;
    }
    .news-info span{
        margin-right: 20px;
    }
    i{
        font-size: 12px;
    }
    .comments{
        margin-left: 5px;
    }

    /* 底部页面ID */
    .page-line{
        margin-right: 11vw;
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
    .next-page, .prev-page{
        background-color:rgba(0, 0, 0, .05);
        border-radius: 5px;
        height: 30px;
        width: 60px;
        margin: 0 8px;
    }
    .page-line i{
        display: block;
        line-height: 30px;
        text-align: center;
        font-size: 22px;
    }
    @media(max-width: 1300px){
        .aside{
            right:50px;
        }
    }
</style>


